*{
    padding: 0;
    margin: 0;
}
:root{
    --color : #dcb171;
    --bg-color : linear-gradient(45deg,#dcb171,#f3d6aa);
}
html{
    font-size: 62.5%;
    scroll-behavior: smooth;
}
a{
    text-decoration: none;
}
li{
    list-style: none;
}

@media screen and (max-width: 992px) {
    html{
        font-size: 55.5%;
    }
}
@media screen and (max-width: 425px) {
    html{
        font-size: 50.5%;
    }
}